// __tests__/components/exercise-common/CategorySelector.test.js
import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import { Text, View } from 'react-native';
import CategorySelector from '../../../src/components/exercise-common/CategorySelector';

// Crée des mocks pour les méthodes de l'animation
const mockStart = jest.fn();

// Mock pour Animated.Value qui retourne un objet avec les méthodes nécessaires
const createMockAnimatedValue = (initialValue = 0) => ({
  interpolate: jest.fn(() => ({})),
  setValue: jest.fn(),
  addListener: jest.fn(),
  removeListener: jest.fn(),
  removeAllListeners: jest.fn(),
  stopAnimation: jest.fn(),
  resetAnimation: jest.fn(),
  _value: initialValue,
  _offset: 0,
  _animation: null,
});

// Remplace l'implémentation d'Animated avec des mocks
jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  
  // Créer le mock d'Animated.timing à l'intérieur du mock
  const mockAnimatedTiming = jest.fn(() => ({
    start: jest.fn(),
  }));
  
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: jest.fn((initialValue) => createMockAnimatedValue(initialValue)),
      timing: mockAnimatedTiming,
      View: ({ children, style, ...props }) => <RN.View style={style} {...props}>{children}</RN.View>,
    },
    View: RN.View,
    Text: RN.Text,
    TouchableOpacity: RN.TouchableOpacity,
    ScrollView: RN.ScrollView,
  };
});

// Mock d'expo-linear-gradient
jest.mock('expo-linear-gradient', () => {
  const { View } = require('react-native');
  return {
    LinearGradient: (props) => <View {...props} />,
  };
});

// Mock du fichier de style
jest.mock('../../../src/components/exercise-common/CategorySelector/style', () => {
  const { StyleSheet } = require('react-native');
  return () => StyleSheet.create({
    container: {},
    backgroundGradient: {},
    scrollView: {},
    scrollContent: {},
    categoryItemWrapper: {},
    categoryTouchable: {},
    selectedCategoryItem: {},
    selectedInner: {},
    selectedCategoryText: {},
    sparkleContainer: {},
    sparkle: {},
    categoryItem: {},
    categoryInner: {},
    categoryText: {},
  });
});

describe('CategorySelector', () => {
  const categories = [
    { id: '1', name: 'Grammar' },
    { id: '2', name: 'Vocabulary' },
    { id: '3', name: 'Reading' },
  ];

  // Récupérer le mock d'Animated.timing après l'import
  const { Animated } = require('react-native');
  const mockAnimatedTiming = Animated.timing;

  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('should render all categories correctly with minimum props', () => {
    const { getByText } = render(<CategorySelector categories={categories} />);
    expect(getByText('Grammar')).toBeTruthy();
    expect(getByText('Vocabulary')).toBeTruthy();
    expect(getByText('Reading')).toBeTruthy();
  });

  it('should render nothing when the categories array is empty', () => {
    const { queryByText } = render(<CategorySelector categories={[]} />);
    expect(queryByText('Grammar')).toBeNull();
  });

  it('should display the selected category with the sparkle emoji', () => {
    const { getByText } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={() => {}}
      />
    );
    expect(getByText('✨')).toBeTruthy();
  });

  it('should call onSelectCategory with the correct category id when a category is pressed', () => {
    const onSelectCategory = jest.fn();
    const { getByText } = render(
      <CategorySelector
        categories={categories}
        onSelectCategory={onSelectCategory}
      />
    );
    fireEvent.press(getByText('Vocabulary'));
    expect(onSelectCategory).toHaveBeenCalledWith('2');
  });

  it('should deselect the previous category when a new one is selected and trigger two animations', () => {
    const onSelectCategory = jest.fn();

    // 1. On rend le composant avec une catégorie sélectionnée
    const { getByText } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={onSelectCategory}
      />
    );

    // Clear le mock après le rendu initial
    mockAnimatedTiming.mockClear();

    // 2. On simule le clic sur une autre catégorie
    fireEvent.press(getByText('Vocabulary'));
    
    // 3. Vérifier que onSelectCategory a été appelé
    expect(onSelectCategory).toHaveBeenCalledWith('2');
    
    // 4. Vérifier que les deux animations ont été déclenchées
    expect(mockAnimatedTiming).toHaveBeenCalledTimes(2);
    
    // 5. Vérifier les paramètres des animations
    const calls = mockAnimatedTiming.mock.calls;
    
    // Premier appel : désélection (toValue: 0, duration: 200)
    expect(calls[0][1]).toEqual(expect.objectContaining({
      toValue: 0,
      duration: 200,
      useNativeDriver: false,
    }));
    
    // Deuxième appel : sélection (toValue: 1, duration: 300)
    expect(calls[1][1]).toEqual(expect.objectContaining({
      toValue: 1,
      duration: 300,
      useNativeDriver: false,
    }));
  });
});